import React from 'react';

export default class Wizard extends React.Component {
    constructor(props) {
        super(props);

        this.state = {};
        this._step = null;
    }
    componentDidMount() {
        this.props.onTitleChange(this._step ? this._step.props.title : 'no title yet!');
        const modal = this._step && this._step.props.modal;
        this.props.setModal(modal);
    }
    componentDidUpdate(prevProps, prevState, snapshot) {
        if (prevProps.curStep !== this.props.curStep) {
            this.props.onTitleChange(this._step ? this._step.props.title : 'no title yet!');
        }
        if (prevProps.curStep !== this.props.curStep) {
            const modal = this._step && this._step.props.modal;
            this.props.setModal(modal);
        }
    }
    _getStep() {
        const { curStep } = this.props;
        return React.Children.map(this.props.children, (child, i) => {
            if (i !== curStep) return;
            return child;
        })[0];
    }
    render() {
        const { curStep } = this.props;
        const childUB = React.Children.count(this.props.children) - 1;
        this._step = this._getStep();

        const prevOk = this._step && this._step.props.prevOkIf ? this._step.props.prevOkIf() : true;
        const nextOk = this._step && this._step.props.nextOkIf ? this._step.props.nextOkIf() : true;
        const cta = this._step.props.cta || '';
        const prevDisabled = curStep <= 0 || !prevOk;
        const nextDisabled = curStep >= childUB || !nextOk;
        const finishDisabled = !nextOk;
        return (
            <div className='wizard'>
                <div className='body'>{this._step}</div>
                <div className='cta-footer'>{cta}</div>
                <div className='footer'>
                    <button disabled={prevDisabled} onClick={this.props.prevClicked}>|&lt; Prev</button>
                    <button style={curStep >= childUB ? { display: 'none' } : {}} disabled={nextDisabled} onClick={this.props.nextClicked}>Next &gt;|</button>
                    <button style={curStep < childUB ? { display: 'none' } : {}} disabled={finishDisabled} onClick={this.props.finishClicked}>Finish</button>
                </div>
            </div>
        )
    }
}